<template>
  <div class="alert" v-if="wrongData">
    <div class="alert__title">
       
    </div>
    <div class="alert__description">
          .
    </div>
  </div>
  <section class="auth">
    <h1>  </h1>
    <form class="form" @submit.prevent="getAccess">
      <div class="form-item">
        <input type="email" required v-model.trim="userData.email" />
        <label>Email</label>
      </div>
      <div class="form-item">
        <input type="password" required v-model.trim="userData.password" />
        <label></label>
      </div>
      <div class="form-submit">
        <button>
          
        </button>
      </div>
    </form>
    <div class="auth-footer">
      <p>
          ?
        <router-link to="#"> </router-link> <br />
          ?
        <router-link to="/register">  !</router-link>
      </p>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      userData: {
        email: "",
        password: "",
      },
      wrongData: null,
    };
  },
  computed: {
    isAuthenticated() {
      return this.$store.getters.isAuthenticated;
    },
  },
  methods: {
    async getAccess() {
      try {
        await this.$store.dispatch("login", {
          email: this.userData.email,
          password: this.userData.password,
        });
        if (this.$store.getters.isAuthenticated) {
          this.$router.push("catalogue");
        }
      } catch (err) {
        console.log(err);
        this.userData.email = "";
        this.userData.password = "";
        this.wrongData = true;
        this.hideAlert();
      }
    },
    hideAlert() {
      setTimeout(() => this.wrongData = null, 7000)
    }
  },
};
</script>

<style lang="scss" scoped>
@import './form-styles.scss';
</style>
